/*gloabal document*/
import React, { Component } from "react";
import "./App.css";
import NeuralNetwork from "./lib/nn";
const HEIGHT = 500;
const WIDTH = 800;
const MIN_PIPE_HEIGHT = 70;
const PIPE_WIDTH = 30;
const FPS = 240;
const BIRD_COUNT = 250;
let MAXFRAMECOUNT = 0;

class Bird {
  constructor(ctx, brain) {
    this.ctx = ctx;
    this.x = 150;
    this.y = 250;
    this.gravity = 1;
    this.velocity = 0.2;
    this.frameCount = 0;
    this.mutated = false;

    if (brain instanceof NeuralNetwork) {
      this.brain = brain.copy();
      this.mutate();
    } else {
      this.brain = new NeuralNetwork(5, 5, 2);
    }
  }

  mutate = () => {
    this.brain.mutate(x => {
      if (Math.random() < 0.1) {
        this.mutated = true;
        const offset = Math.random();
        return x + offset;
      }
      return x;
    });
  };
  draw() {
    this.ctx.fillStyle = this.mutated ? "green" : "black";
    this.ctx.beginPath();
    this.ctx.arc(this.x, this.y, 10, 0, 2 * Math.PI);
    this.ctx.fill();
  }
  update(pipeX, spaceStartY, spaceEndY) {
    this.frameCount += 1;
    this.gravity += this.velocity;
    if (this.gravity > 1) this.gravity = 1;
    this.y += this.gravity;
    if (this.y <= 0) {
      this.y = 0;
      this.isDead = true;
    }
    if (this.y >= HEIGHT) {
      this.y = HEIGHT;
      this.isDead = true;
    }
    this.think(pipeX, spaceStartY, spaceEndY);
  }
  jump(x) {
    this.gravity = x;
  }

  think(pipeX, spaceStartY, spaceEndY) {
    const inputs = [
      ((pipeX - this.x) / (WIDTH - this.x)).toFixed(2),
      (spaceStartY / HEIGHT).toFixed(2),
      (spaceEndY / HEIGHT).toFixed(2),
      (this.y / HEIGHT).toFixed(2),
      (this.gravity / 3).toFixed(2)
    ];

    const result = this.brain.predict(inputs);
    if (result[0] < result[1]) {
      this.jump(-3);
    }
  }
}

class Pipe {
  constructor(ctx, height, space) {
    this.ctx = ctx;
    this.x = WIDTH;
    this.y = height ? HEIGHT - height : 0;
    this.width = PIPE_WIDTH;
    this.height =
      height ||
      MIN_PIPE_HEIGHT +
        Math.random() * Math.round(HEIGHT - (space + 2 * MIN_PIPE_HEIGHT));
  }
  draw() {
    this.ctx.fillStyle = "#000";
    this.ctx.fillRect(this.x, this.y, this.width, this.height);
  }
  update() {
    this.x -= 1;
    if (this.x + PIPE_WIDTH < 0) this.isDead = true;
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.canvasRef = React.createRef();
    this.space = 100;
    this.frameCount = 0;
    this.generationCount = 0;
    this.pipes = [];
    this.birds = [];
  }
  componentDidMount() {
    //document.addEventListener("keydown", this.onKeyDown);
    const ctx = this.getCtx();
    this.pipes = this.generatePipes();
    this.birds = this.generateBirds(ctx);
    this.loop = setInterval(this.gameLoop, 1000 / FPS);
  }
  // onKeyDown = e => {
  //   if (this.birds[0] && e.code === "Space") {
  //     this.birds[0].jump(-3);
  //   }
  // };

  generateBirds(ctx) {
    const bbb = [];
    for (let i = 0; i < BIRD_COUNT; i++) {
      let b = new Bird(
        ctx,
        this.smartestBird ? this.smartestBird.brain : undefined
      );
      bbb.push(b);
    }
    return bbb;
  }

  generateNewGeneration(ctx) {
    this.generationCount += 1;
    this.birds = this.generateBirds(ctx);
  }

  getCtx = () => this.canvasRef.current.getContext("2d");

  generatePipes = () => {
    const ctx = this.getCtx();
    const firstPipe = new Pipe(ctx, null, this.space);
    const secondPipeHeight = HEIGHT - (firstPipe.height + this.space);
    const secondPipe = new Pipe(ctx, secondPipeHeight, this.space);
    return [firstPipe, secondPipe];
  };
  gameLoop = () => {
    this.update();
    this.draw();
  };

  update = () => {
    this.frameCount = this.frameCount + 1;
    if (this.frameCount % 400 === 0) {
      const pipes = this.generatePipes();
      this.pipes.push(...pipes);
    }
    if (this.birds.length === 0 && this.frameCount % 100 === 0) {
      this.generateNewGeneration(this.getCtx());
    }
    this.pipes.forEach(pipe => pipe.x--);
    this.pipes = this.pipes.filter(x => !x.isDead);

    if (this.birds.length === this.birds.filter(x => x.isDead).length) {
      if (this.birds[0] && this.birds[0].frameCount > MAXFRAMECOUNT) {
        MAXFRAMECOUNT = this.birds[0].frameCount;
        this.smartestBird = this.birds[0];
      }
    }
    this.birds = this.birds.filter(x => !x.isDead);

    if (this.birds.length > 0) {
      if (this.birds[0].frameCount > MAXFRAMECOUNT) {
        MAXFRAMECOUNT = this.birds[0].frameCount;
        this.smartestBird = this.birds[0];
      }
    }
    this.birds.forEach(bird => {
      const nextPipe = this.getNextPipe(bird);
      const spaceStartY = nextPipe.y + nextPipe.height;
      bird.update(nextPipe.x, spaceStartY, spaceStartY + this.space);
    });

    if (this.isGameOver()) {
      clearInterval(this.loop);
      alert("GAME OVER");
    }
  };

  getNextPipe = bird => {
    for (let i = 0; i < this.pipes.length; i++) {
      if (this.pipes[i].x > bird.x) {
        return this.pipes[i];
      }
    }
  };

  isGameOver = () => {
    let gameOver = false;
    this.birds.forEach(bird => {
      this.pipes.forEach(pipe => {
        if (
          (bird.x > pipe.x &&
            bird.x < pipe.x + pipe.width &&
            bird.y > pipe.y &&
            bird.y < pipe.y + pipe.height) ||
          bird.y > HEIGHT ||
          bird.y < 0
        )
          bird.isDead = true;
        gameOver = false;
      });
    });
    return gameOver;
  };

  draw = () => {
    const ctx = this.canvasRef.current.getContext("2d");
    ctx.clearRect(0, 0, WIDTH, HEIGHT);
    this.pipes.forEach(pipe => pipe.draw());
    this.birds.forEach(bird => bird.draw());
    ctx.fillStyle = "#000";
    ctx.fillText(`Nesil: ${this.generationCount}`, 15, 15);
    ctx.fillText(`Ku≈ü: ${this.birds.length}`, 15, 30);
    ctx.fillText(`Score: ${(MAXFRAMECOUNT / FPS).toFixed(1)} sn`, 15, 45);
  };

  render() {
    return (
      <div className="App">
        <canvas
          ref={this.canvasRef}
          height={HEIGHT}
          width={WIDTH}
          style={{ marginTop: "24px", border: "1px solid #c3c3c3" }}
        ></canvas>
      </div>
    );
  }
}

export default App;
